import 'assets/css/gridMain.css';
import 'assets/css/batteries.css';
import 'react-datepicker/dist/react-datepicker.css';
import { Grid } from 'gridjs-react';
import 'gridjs/dist/theme/mermaid.min.css';
import DatePicker from 'react-datepicker';
import { useState } from 'react';
import { data, gridColumn } from 'app/data/deviceManagement/batteries/gridData';
import { useNavigate } from 'react-router-dom';

function Batteries() {
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const statusList = ['상태1', '상태2'];
  const [status, setStatus] = useState('');
  const navigate = useNavigate();

  const gridRowClick = (e) => {
    const rowObj = {};
    if (e.target.getAttribute('data-column-id') !== null) {
      e.target.parentNode.childNodes.forEach((obj) => {
        rowObj[`${obj.getAttribute('data-column-id')}`] = obj.textContent;
      });
      rowObj.flag = '수정';
      console.log(rowObj);
      navigate('/deviceManagement/batteries/addOrUpdate', {
        state: rowObj,
      });
    }
  };

  return (
    <div className="section__grid__wrap content grid__main batteries">
      <section className="grid__main__search">
        <div className="section__inner">
          <div className="search__wrap">
            <div className="selector__wrap">
              <dl>
                <dt>조회기간</dt>
                <dd>
                  <DatePicker selected={startDate} onChange={(date) => setStartDate(date)} />
                  <DatePicker selected={endDate} onChange={(date) => setEndDate(date)} />
                </dd>
              </dl>
              <dl>
                <dt>조회그룹</dt>
                <dd>
                  <select className="custom__select" onChange={(e) => setStatus(e.target.value)}>
                    <option value="">전체 상태</option>
                    {statusList.map((obj, idx) => (
                      <option key={obj} value={obj}>
                        {obj}
                      </option>
                    ))}
                  </select>
                </dd>
              </dl>
              <dl>
                <dt>운영상태</dt>
                <dd>
                  <select className="custom__select" onChange={(e) => setStatus(e.target.value)}>
                    <option value="">전체 상태</option>
                    {statusList.map((obj, idx) => (
                      <option key={obj} value={obj}>
                        {obj}
                      </option>
                    ))}
                  </select>
                </dd>
              </dl>
            </div>
            <div className="input__wrap">
              <dl>
                <dt>장치명</dt>
                <dd>
                  <input
                    type="text"
                    className="custom__input"
                    placeholder="장치이름을 입력하세요"
                  />
                </dd>
              </dl>
              <ul>
                <li>
                  <button className="custom__btn">
                    <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                      />
                    </svg>
                    <span className="f__medium">검색</span>
                  </button>
                </li>
                <li>
                  <button
                    className="custom__btn"
                    onClick={(e) => {
                      navigate('/deviceManagement/batteries/addOrUpdate', {
                        state: { flag: '등록' },
                      });
                    }}
                  >
                    <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                      />
                    </svg>
                    <span className="f__medium">등록</span>
                  </button>
                </li>
                <li>
                  <button className="custom__btn">
                    <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                      />
                    </svg>
                    <span className="f__medium">Excel등록</span>
                  </button>
                </li>
              </ul>
            </div>
          </div>
          <div
            className="grid__wrap"
            onClick={(e) => {
              gridRowClick(e);
            }}
          >
            <Grid
              sort="true"
              className="grid"
              data={data}
              columns={gridColumn}
              pagination={{
                enabled: true,
                limit: 10,
              }}
            />
          </div>
        </div>
      </section>
    </div>
  );
}

export default Batteries;
